.variety {
  display: flex;
  background: linear-gradient(112.25deg, #005BFF 29.47%, #36A6E8 104.07%);
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.6);
  border-radius: 0px;

  &__container {
    display: flex;
    flex-direction: column;
    max-width: 1306px;
    width: 100%;
    margin: 0 auto;
    padding-top: 70px;
    padding-left: 50px;
    padding-right: 50px;

  }

  &__title {
    color: $white;
    margin-bottom: 81px;
  }

  &__list {
    display: grid;
    grid-template-columns: 33% 33% 33%;
    justify-content: space-between;
    column-gap: 37px;
    padding-right: 30px;

    margin-bottom: 81px;
  }

  &__list-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    filter: drop-shadow(0px 4px 34px rgba(0, 0, 0, 0.25));
    border-radius: 17px;
    max-width: 357px;
    width: 100%;
    padding: 37px 20px 32px 20px;
    text-align: center;

    &--first {
      background-color: $lemon;

      .variety__subtitle {
        background-color: $light-yellow;
      }
    }

    &--second {
      background-color: $pink;

      .variety__subtitle {
        background-color: $light-pink;
        ;
      }
    }

    &--third {
      background-color: $whitish;
      padding-top: 30px;

      .variety__subtitle {
        background-color: $mint;
      }
    }

    svg {
      margin-bottom: 32px;
    }
  }

  &__subtitle {
    font-weight: 600;
    font-size: 30px;
    line-height: 35px;
    margin-bottom: 32px;
    border-radius: 17px;
    width: 100%;
    text-align: center;
    padding-top: 25px;
    padding-bottom: 21px;
    white-space: nowrap;
  }

  &__txt {
    font-weight: 600;
    font-size: 20px;
  }
}
