.advantages {
  display: flex;
  background: linear-gradient(112.25deg, #005BFF 29.47%, #36A6E8 104.07%);
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.6);
  border-radius: 160px 0px;
  color: $white;
  margin-bottom: 20px;

  &__container {
    display: flex;
    flex-direction: column;
    max-width: 1285px;
    width: 100%;
    margin: 0 auto;
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 76px;
  }

  &__title {
    font-size: 30px;
    line-height: 35px;
    margin-bottom: 12px;
  }

  &__subtitle {
    font-size: 22px;
    text-align: center;
    margin-bottom: 85px;
  }
}
